import { Component, OnInit } from '@angular/core';
declare var $;
@Component({
  selector: 'app-payoutchart',
  templateUrl: './payoutchart.html',
  styleUrls: ['./payoutchart.component.css'] 
})
export class PayoutchartComponent implements OnInit {

  constructor() { }

  ngOnInit() {

        /* jQuery activation and setting options for parent tabs with id selector*/
        $("#tabbed-nav").zozoTabs({
            rounded: false,
            multiline: true,
            theme: "white",
            size: "medium",
            responsive: true,
            animation: {
                effects: "slideH",
                easing: "easeInOutCirc",
                type: "jquery"
            },
            defaultTab: "tab2",
            orientation: "horizontal"
        });

        /* jQuery activation and setting options for nested tabs with class selector*/
        $(".nested-tabs").zozoTabs({
            position: "top-left",
            theme: "red",
            style: "underlined",
            rounded: false,
            shadows: false,
            defaultTab: "tab1",
            animation: {
                easing: "easeInOutCirc",
                effects: "slideV"
            },
            size: "medium"
        });
    
  }

}



